// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

// Custom styles

button {
	outline: none;
}
.index-wrapper {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100vh;
	position: relative;
	overflow: hidden;

	&.water {
		justify-content: flex-start;
		overflow: auto;
		height: auto;
	}
	&.main {
		justify-content: space-between;
	}
}
.page-wrapp {

	padding-left: 30px;
	padding-right: 30px;
	.container {
		max-width: 960px;
		width: 100%;
		margin: 0 auto;
		@media (max-width: 1400px) {
			 padding-right: 200px;
			}
		@media (max-width: 847px) {
			padding-right: 0;
		}
	}
	.navigation__wrapper {
		position: fixed;
		top: 250px;
		right: 20px;
		@media (max-width: 847px) {
			display: none;
		}
	}
	.navigation {

		width: 180px;
		height: 280px;
		border: 2px solid #f1f2f7;
		background-color: #ffffff;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;

 		&__title {
			color: #58667f;
 			font-size: 18px;
			font-weight: 500;
 			margin-bottom: 23px;
			padding-left: 20px;

			margin-top: 29px;
		}
		&__link {
			color: #7f8489;
			font-family: Lato;
			font-size: 14px;
			font-weight: 400;
 			text-decoration: none;
			margin-bottom: 20px;
			padding-left: 20px;
			&:active {
				color: #2f9ba8;
				text-decoration: underline;
			}
		}
		&__btn {
			width: 180px;
			height: 41px;
			border: 2px solid #f1f2f7;
			background-color: #ffffff;
			color: #58667f;
 			font-size: 14px;
			font-weight: 500;
			line-height: 36px;
			cursor: pointer;
			margin-top: 50px;
			padding-left: 20px;
			position: relative;
			&:after {
				position: absolute;
				content: '';
				top: 14px;
				left: 50px;
				transform: rotate(180deg);
				width: 13px;
				height: 9px;
				background: url("../images/sprites/png/dropdown.png") center no-repeat;
			}
		}
	}

}

.page-divider {
	width: 100%;
	height: 0;
	border: 2px solid #c2c2c2;
	opacity: 0.4;
	margin-top: 73px;
	margin-bottom: 70px;
	@media(max-width: 768px) {
		margin-top: 40px;
		margin-bottom: 40px;
	}
}
.overlay {
	position: fixed;
	display: none;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0,0,0,0.5);
	z-index: 10;
	cursor: pointer;
	&.show {
		display: block;
	}
}
.topnav {
	background-color: #fff;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 10px 30px 10px 30px;
	z-index: 10;
	@media(max-width: 456px) {
		padding: 11px 20px;
	}
	.dropdown {
		position: relative;

		&:nth-of-type(2){
			@media (max-width: 1449px) {
				display: none;
			}
		}



		.dropbtn {
			cursor: pointer;

			font-size: 18px;
			font-weight: 400;
			line-height: 27.52px;
			text-transform: uppercase;
			letter-spacing: 0.72px;
			color: #205880;
			background-color: transparent;
			border: none;
			position: relative;
			&:after {
				content: '';
				position: absolute;
				top: 10px;
				right: -15px;
				width: 13px;
				height: 9px;
				background: url("../images/sprites/png/dropdown.png") center no-repeat;
				@media (max-width: 785px) {
					top: 23px;

 				}
			}

		}

		@media (max-width: 1440px){
			padding-right: 15px;
		}


		@media (max-width: 518px){
			display: none;
		}



		.dropdown-content {
			display: none;
			position: absolute;
			background-color: #f9f9f9;
			max-width: 300px;
			width: 100%;
			box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
			z-index: 1;
			a {
				background-color: #ebebeb;
				color: #205880;
				padding: 12px 16px;
				text-decoration: none;
				display: block;
				text-align: left;

				&:hover {
					transition: .3s ease;
					background-color: #f9f9f9;
				}
			}
		}
		&:hover {
			.dropdown-content {
				display: block;
			}
		}
	}


	&__contacts {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		padding-left: 37px;
		@media (max-width: 1048px) {
			display: none;
		}

		@media (max-width: 518px){
			display: flex;
		}
		.phone {
			position: relative;
			color: #505254;
			font-size: 18px;
			font-weight: 400;
			line-height: 25px;
			text-decoration: none;
			margin-bottom: 11px;
			&:after {
				position: absolute;
				content: '';
			    top: 3px;
   			    left: -32px;
				width: 24px;
				height: 24px;
				background: url('../images/sprites/png/phone.png') center no-repeat;

			}
		}

		.email {
			color: #2f9ba8;
			font-size: 16px;
			font-weight: 400;
			line-height: 25px;
			text-decoration: underline;
			position: relative;
			&:after {
				position: absolute;
				content: '';
			    top: 3px;
   			    left: -32px;
				width: 24px;
				height: 24px;
				background: url('../images/sprites/png/mail.png') center no-repeat;

			}
		}
	}
	.icon {
		font-size: 24px;
		color: #205880;
		cursor: pointer;
	}
	&.page-content {

		@media (max-width: 518px) {
			background-color: #1c5781;

 			.topnav__contacts {
				display: none;
			}
			.topnav__logo-mob {
				color: #feffff;
				font-family: "Myriad Pro";
				font-size: 22px;
				font-weight: 400;
 				text-decoration: none;
			}
			.icon {
				color: #fff;
				order: 2;
				@media(max-width: 518px){
					display: none;
				}
			}
			.topnav__burger {
				order: 2;
				.bar1, .bar2, .bar3 {
					width: 25px;
					height: 4px;
					background-color: #fff;
					margin: 5px 0;
					transition: 0.4s;
					border-radius: 5px;


				}

				/* Rotate first bar */
			&.change {
				.bar1 {
					transform: rotate(-45deg) translate(-5px, 4px);
				}

				/* Fade out the second bar */
				.bar2 {
					opacity: 0;
				}

				/* Rotate last bar */
				.bar3 {
					transform: rotate(45deg) translate(-8px, -8px);
				}			}
			}
		}

		@media (min-width: 518px) {
			.topnav__logo-mob {
				display: none;
			}
		}

		.topnav__burger{
			@media (min-width:518px) {
				display: none;
			}
		}
	}

}
.topnav__contacts-mob {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 3px 25px;
	background-color: #13486e;
	.pnone-wrap {
		display: flex;
		align-items: center;
	}

	.phone {
		opacity: 0.5;
		color: #ffffff;
		font-family: "Lato - Semibold";
		font-size: 12px;
		font-weight: 400;
		line-height: 25px;
		margin-left: 5px;
		text-decoration: none;

	}

	.email {
 		opacity: 0.5;
		color: #ffffff;
		font-family: "Lato - Semibold";
		font-size: 12px;
		font-weight: 400;
		line-height: 25px;
		margin-left: 5px;
		text-decoration: none;

		//&:after {
		//	position: absolute;
		//	content: '';
		//	top: 4px;
		//	left: -27px;
		//	width: 18px;
		//	height: 18px;
		//	background: url('../images/sprites/png/mail-mob.png') center no-repeat;
		//
		//}
	}
	@media (min-width: 518px){
		display: none;
	}
}

.topnav__fixed-wrap {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;
	box-shadow: 0 4px 14px -2px #808080a3;
}
.sidebar {
 	height: 100%;
	width: 350px;
	background-color: #f9f9f9;
	position: fixed;
	top: 0;
	padding-top: 150px;
	box-shadow: 8px 0 5px -2px rgba(124, 124, 124, 0.43);
	z-index: 11;
	overflow: auto;
	.dropdown {
		margin-bottom: 30px;
		.dropbtn {
			padding: 6px 8px 6px 16px;
			text-decoration: none;
			font-size: 15px;
			color: #205880;
			text-transform: uppercase;
			display: block;
			border: none;
			background: none;
			width: 100%;
			text-align: left;
			cursor: pointer;
			outline: none;
			position: relative;
			&:after {
				content: '';
				position: absolute;
				top: 8px;
				right: 19px;
				width: 13px;
				height: 9px;
				background: url("../images/sprites/png/dropdown.png") center no-repeat;
			}
		}

		.dropdown-content {
			display: none;
			max-width: 320px;
			width: 100%;
			box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
			z-index: 1;
 			a {
				background-color: #ebebeb;

				color: #205880;
				padding: 12px 16px;
				text-decoration: none;
				display: block;
				text-align: left;
				&:hover {
					transition: .3s ease;
					background-color: #f9f9f9;
				}
			}

			.topnav__contacts {
				background-color: #ebebeb;
				.phone{


					&:after {
						top: 14px;
						left: -20px;
					}
				}
				.email{


					&:after {
						top: 14px;
						left: -20px;
					}
				}
			}
		}
		&.active {
			.dropdown-content {
				display: block;
				background-color: #fafafa;
			}
			.dropbtn {
				background-color:#f9f9f9;
			}
		}
	}
	&__close {
		font-size: 33px;
		float: right;
		background-color: transparent;
		border: none;
		color: #1c5781;
		top: 20px;
		left: 12px;
		position: fixed;
		margin-right: 10px;
		margin-bottom: 30px;
		cursor: pointer;
	}
	&.closed {
		display: none;
	}
	&.open {
		display: block;
	}
	&.page-content {
		//background-color: red;comeback
		@media (max-width: 518px) {
		 	width: 100%;
			padding-top: 150px;
			.dropdown-content {
				max-width: none;
			}
			.sidebar__close {
				display: none;
 			}

		}

	}
}
.video {
	position: fixed;
	right: 0;
	bottom: 0;
	z-index: -1;
	min-width: 100%;
	min-height: 100%;
	background-color: rgba(70, 70, 70, 0.3);
}

.main {
	&__content {
		position: fixed;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		max-width: 452px;
		text-align: center;
	}
	&__title {
		color: #ffffff;
		font-weight: 100;
		font-size: 70px;
		line-height: 80px;
		font-family: "Lato Thin";
		@media (max-width: 471px) {
			font-size: 50px;
		}

		span {
			font-family: "Mint Spirit";
			font-size: 70px;
			font-weight: bold;
			line-height: 80px;
			margin-left: 15px;
			@media (max-width: 471px) {
				font-size: 50px;
			}
		}
	}

	&__bottom-menu {
		align-self: center;
		flex-wrap: wrap;
		display: flex;
		flex-direction: row;
		justify-content: center;
		@media(max-width: 768px) {
			display: none;
		}
 		&-link {
			color: #ffffff;
			font-size: 16px;
			font-family: "Lato - Semibold";
			line-height: 70px;
			text-transform: uppercase;
			text-decoration: none;
			margin-right: 44px;
			@media (max-width: 905px) {
				margin-right: 20px;
			}
			&:last-of-type {
				margin-right: 0;
			}
			&.logo {
				color: #ffffff;
				font-family: "Mint Spirit";
				font-size: 24px;
				font-weight: 400;
				line-height: 68.21px;
				text-transform: none;
				margin-right: 63px;
				@media (max-width: 500px) {
					width: 100%;
					text-align: center;
					line-height: 1;
					margin-bottom: 25px;
					margin-right: 0;
				}
				@media (max-width: 800px) {
					margin-right: 30px;
				}


			}
			@media (max-width: 500px) {
				width: 100%;
				text-align: center;
				line-height: 1;
				margin-bottom: 15px;
				margin-right: 0;
			}
		}
 	}
}

