.protocol-inner {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 22px;
	flex-wrap: wrap;
	@media(max-width: 551px){
		justify-content: center;
	}
 	.protocol {
		&__header {
			position: absolute;
			color: #1c5781;
			font-family: Lato;
			font-size: 32px;
			font-weight: 300;
			line-height: 27.52px;
			width: 254px;
			top: -58px;
			left: 0;

			&.color {
				color: #00a08c;
			}
		}
		&-item {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			width: 236px;
			height: 414px;
			margin-top: 20px;
			padding: 0 29px;
			position: relative;

 			&:first-of-type {
				background-color: #448dc3;
			}
			&:nth-child(2) {
				background-color: #2b75a7;
			}
			&:nth-child(3) {
				background-color: #009f8c;
			}
			&:nth-child(4) {
