			//	margin-right: 167px;
			//}
			//&:last-of-type {
			//	margin-left: 138px;
			//}
		}
	}
	&__list-title {
		span {
			display: block;
			color: #505254;
			font-size: 17px;
			font-family: "Lato - Semibold";
			line-height: 20px;
			position: relative;
			margin-bottom: 27px;
			max-width: 185px;
			@media (max-width: 568px) {
				max-width: none;
			}
 			&:before {
				position: absolute;
				content: '';
				top: 0;
				left: -77px;
				width: 55px;
				height: 55px;

				@media (max-width: 568px) {
					top: -72px;
					left: -7px;
				}
			}
			&:after {
				position: absolute;
